@layer components {

  .form-label {
    @apply block mb-2 text-gray-700 select-none;
  }

  .form-input,
  .form-textarea,
  .form-select {
    @apply relative block w-full p-2 font-sans leading-normal text-left text-gray-700 bg-white border rounded appearance-none;

    &:focus,
    &.focus {
      @apply border-indigo-500;
      box-shadow: 0 0 0 1px theme('colors.indigo.500');
    }

    &::placeholder {
      @apply text-gray-500 opacity-100;
    }
  }

  .form-select {
    @apply pr-6;

    background-image: url('data:image/png;base64,');
    background-size: 0.7rem;
    background-repeat: no-repeat;
    background-position: right 0.7rem center;

    &::-ms-expand {
      @apply opacity-0;
    }
  }

  .form-error {
    @apply mt-2 text-sm text-red-700;
  }

  .form-input.error,
  .form-textarea.error,
  .form-select.error {
    @apply border-red-600;

    &:focus {
      box-shadow: 0 0 0 1px theme('colors.red.600');
    }
  }
  
}
